@media (max-width: 767px) {
  .toggle {
    position: relative;
    width: 30px;
    height: 30px;
    cursor: pointer;
    background: url(../img/menu.webp);
    filter: invert(1);
    background-size: 30px;
    background-position: center;
    background-repeat: no-repeat;
    z-index: 10000;
  }

  .toggle.active {
    position: fixed;
    top: 30px;
    right: 25px;
    background: url(../img/close.webp);
    filter: invert(1);
    width: 25px;
    height: 25px;
    background-size: 25px;
    background-position: center;
    background-repeat: no-repeat;
    z-index: 10001;
  }

  .navigation.active {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    flex-direction: column;
    z-index: 10000;
    background: #fff;
  }

  .navigation.active li {
    margin-left: 0;
  }

  .navigation.active li a {
    font-size: 1.8em;
    margin: 5px 0;
    display: inline-block;
    border-radius: 30px;
  }
