@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,900&family=Rancho&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
//font-family: 'Poppins', sans-serif;

//font-family: 'Rancho', cursive;
@import "preload";
@import "close";
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins, sans-serif;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}
//---------------- section
.container {
  position: relative;
  padding: 0 100px;
  min-height: 100vh;
}
.container::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 350px;
  height: 100%;
  background: #333;
  z-index: -2;
}
.container::after {
  content: "Healthy";
  position: absolute;
  bottom: 0;
  left: 0;
  font-size: 18vw;
  font-weight: 700;
  z-index: -2;
  color: #000;
  opacity: 0.08;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 0;
}
.logo {
  font-weight: 700;
  text-transform: uppercase;
  color: #333;
  font-size: 30px;
}
.logo span {
  color: #78ff00;
}
.navigation {
  position: relative;
  display: flex;
}
.navigation li {
  margin: 0 20px;
}
.navigation li a {
  position: relative;
  font-size: 18px;
  padding: 4px 10px;
  color: #333;
}
.navigation li a::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: #78ff00;
  z-index: -1;
  transition: 0.5s all ease-in-out;
}
.navigation li:nth-child(3) a,
.navigation li:nth-child(4) a {
  color: #fff;
}
.navigation li a:hover::before,
.navigation li a.active::before {
  width: 40px;
}
.content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 50px;
}
.bannerText {
  position: relative;
  max-width: 500px;
  margin-right: 50px;
}
.bannerText h2 {
  font-size: 54px;
  text-transform: uppercase;
  font-weight: 800;
  line-height: 1.2em;
  color: #333;
}
.bannerText p {
  font-size: 18px;
  color: #333;
  line-height: 1.8em;
  margin: 20px 0;
}
.bannerText a {
  position: relative;
  display: inline-block;
  text-transform: uppercase;
  font-weight: 700;
  color: #333;
  letter-spacing: 1px;
  font-size: 18px;
  padding: 15px;
}
.bannerText a::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  height: 100%;
  background: #78ff00;
  z-index: -1;
  transition: 0.5s all ease-in-out;
}
.bannerText a:hover:before {
  width: 100%;
}
.bannerImg {
  position: relative;
  width: 440px;
  height: 440px;
  background: #000;
  min-width: 440px;
}
.bannerImg img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.5s;
  opacity: 0;
}
.bannerImg img.active {
  opacity: 1;
}
.icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px 0;
}
.sci,
.controls {
  position: relative;
  display: flex;
}
.sci li {
  margin-right: 30px;
}
.sci li img {
  display: inline-block;
  max-width: 24px;
  transition: 0.5s all ease-in-out;
}
.sci li:hover img {
  transform: translateY(-5px);
  filter: invert(50%);
}
.controls li {
  margin-left: 20px;
  cursor: pointer;
  filter: invert(1);
  transition: 0.5s all ease-in-out;
  width: 50px;
  height: 50px;
}
.controls li:hover {
  opacity: 0.5;
}
.controls li img {
  width: 100%;
}
.banner__block {
  display: none;
}
.banner__block.active {
  display: initial;
}
.str {
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
@media (max-width: 1190px) {
  .container {
    padding: 20px 50px;
  }
  .bannerText {
    margin-right: 20px;
  }
  .container::before {
    width: 290px;
  }
}
@media (max-width: 992px) {
  .str {
    display: -webkit-box;
    -webkit-line-clamp: 6;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
  .container::after {
    font-size: 16vw;
  }
  .container::before {
    width: 275px;
  }
  .header {
    padding: 0;
  }
  .bannerText h2 {
    font-size: 26px;
  }
  .navigation li {
    margin: 0 15px;
  }
  .bannerText p {
    font-size: 16px;
  }
}
@media (max-width: 767px) {
  .container::before {
    display: none;
  }
  .container::after {
    font-size: 24vw;
  }
  .navigation li:nth-child(3) a,
  .navigation li:nth-child(4) a {
    color: #333;
  }
  .controls li {
    filter: invert(0);
  }
  .navigation {
    display: none;
    padding: 100px 0;
  }
  .content {
    margin-top: 50px;
    flex-direction: column-reverse;
  }
  .bannerText {
    margin-right: 0;
    margin-top: 50px;
    margin-bottom: 50px;
    max-width: 640px;
  }
  .container {
    padding: 20px;
    overflow-x: hidden;
  }
  .icons {
    max-width: 640px;
    margin: 0 auto;
    padding: 30px 0 50px;
  }
  .header {
    padding: 0;
    max-width: 640px;
    margin: 0 auto;
  }
  .bannerImg {
    width: 100%;
    height: 440px;
  }
  .str {
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
}
@media (max-width: 500px) {
  .bannerImg {
    min-width: auto;
    height: 320px;
  }
  .icons {
    flex-direction: column-reverse;
    padding: 0 10px 0;
  }
  .sci {
    align-items: center;
    justify-content: center;
    margin-top: 40px;
  }
  .sci li {
    margin-right: 30px;
  }
  .sci li:last-child {
    margin-right: 0;
  }
  .controls li:first-child {
    margin-left: 0px;
  }
  .navigation li a:hover::before,
  .navigation li a.active::before {
    width: 100%;
  }
}
